let WebIM = require("../../utils/WebIM")["default"];

export default {
  data() {
    return {
      friend_name: "",
      search_btn: true,
      search_chats: false,
      show_clear: false,
      isdisable: false,
      input_code: ""
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    let app = getApp().globalData;
  },
  methods: {
    openSearch: function () {
      this.setData({
        search_btn: false,
        search_chats: true
      });
    },
    onInput: function (e) {
      let inputValue = e.detail.value;

      if (inputValue) {
        this.setData({
          show_clear: true,
          friend_name: inputValue,
          isdisable: false
        });
      } else {
        this.setData({
          show_clear: false
        });
      }
    },
    clearInput: function () {
      this.setData({
        input_code: '',
        show_clear: false
      });
    },
    cancel: function () {
      this.setData({
        search_btn: true,
        search_chats: false,
        show_clear: false
      });
    },
    add_friend: function () {
      let me = this;
      let myName = wx.getStorageSync("myUsername");
      if (me.friend_name == "" || me.friend_name.toLowerCase() == myName.toLowerCase()) {
        uni.showToast({title: "添加失败"});
        return;
      }

      WebIM.conn.subscribe({
        to: me.friend_name,
        message: myName + '请求添加好友'
      });

      let rosters = {
        success: function (roster) {
          console.log('success');
          var member = [];

          for (let i = 0; i < roster.length; i++) {
            if (roster[i].subscription == "both") {
              member.push(roster[i]);
            }
          }

          if (me.isExistFriend(me.friend_name, member)) {
            uni.showToast({
                	title: "已经是你的好友",
                });
          } else {
             uni.showToast({
                	title: "已经发出好友申请",
                });
          }

          me.setData({
            isdisable: true
          });
        }
      };
      WebIM.conn.getRoster(rosters);
    },
    isExistFriend: function (name, list) {
      for (let index = 0; index < list.length; index++) {
        if (name == list[index].name) {
          return true;
        }
      }

      return false;
    }
  }
};
